'use client';

import { useState } from 'react';
import { db } from '@/lib/firebase';
import { collection, writeBatch, doc } from 'firebase/firestore';
import { artworks, posts } from '@/lib/data';
import { Database, UploadCloud, CheckCircle, AlertCircle } from 'lucide-react';

export default function AdminDashboard() {
    const [loading, setLoading] = useState(false);
    const [status, setStatus] = useState<'idle' | 'success' | 'error'>('idle');

    const seedData = async () => {
        if (!confirm('This will overwrite existing data with the local template. Are you sure?')) return;

        setLoading(true);
        setStatus('idle');
        try {
            const batch = writeBatch(db);

            // Seed Artworks
            artworks.forEach(art => {
                const ref = doc(db, 'artworks', art.id);
                batch.set(ref, {
                    ...art,
                    // Ensure dates are not serialized weirdly if they pass through JSON somewhere, 
                    // but here we are using the objects directly.
                    // data.ts has Date objects. Firestore supports Timestamp/Date.
                    updatedAt: new Date(),
                });
            });

            // Seed Posts
            posts.forEach(post => {
                const ref = doc(db, 'posts', post.id);
                batch.set(ref, {
                    ...post,
                    createdAt: new Date(),
                    updatedAt: new Date(),
                });
            });

            await batch.commit();
            setStatus('success');
        } catch (e) {
            console.error(e);
            setStatus('error');
        }
        setLoading(false);
    };

    return (
        <div className="space-y-8 animate-fade-in-up">
            <div>
                <h1 className="text-3xl font-bold text-[var(--text-primary)] mb-2">CMS Dashboard</h1>
                <p className="text-[var(--text-secondary)]">Manage your content and synchronize data.</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                <div className="card p-6 bg-[var(--bg-secondary)] border border-[var(--border-color)]">
                    <div className="flex items-center justify-between mb-4">
                        <div className="p-3 bg-blue-500/10 rounded-lg text-blue-500">
                            <Database className="w-6 h-6" />
                        </div>
                        <span className="text-sm font-medium text-[var(--accent-primary)]">Database</span>
                    </div>
                    <h3 className="text-xl font-bold text-[var(--text-primary)] mb-1">Data Management</h3>
                    <p className="text-[var(--text-secondary)] text-sm mb-4">Seed your database with initial local data.</p>

                    <button
                        onClick={seedData}
                        disabled={loading}
                        className={`w-full py-2 px-4 rounded-lg flex items-center justify-center gap-2 transition-all ${loading ? 'opacity-50 cursor-not-allowed bg-[var(--bg-tertiary)]' : 'bg-[var(--accent-primary)] hover:bg-[var(--accent-secondary)] text-white'
                            }`}
                    >
                        {loading ? <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" /> : <UploadCloud className="w-5 h-5" />}
                        {loading ? 'Seeding...' : 'Seed Database'}
                    </button>

                    {status === 'success' && (
                        <div className="mt-4 flex items-center gap-2 text-green-500 text-sm bg-green-500/10 p-2 rounded">
                            <CheckCircle className="w-4 h-4" />
                            <span>Data seeded successfully!</span>
                        </div>
                    )}
                    {status === 'error' && (
                        <div className="mt-4 flex items-center gap-2 text-red-500 text-sm bg-red-500/10 p-2 rounded">
                            <AlertCircle className="w-4 h-4" />
                            <span>Error seeding data. Check console.</span>
                        </div>
                    )}
                </div>

                {/* Placeholder for Stats */}
                <div className="card p-6 bg-[var(--bg-secondary)] border border-[var(--border-color)] opacity-50">
                    <div className="p-3 bg-purple-500/10 rounded-lg text-purple-500 w-fit mb-4">
                        <CheckCircle className="w-6 h-6" />
                    </div>
                    <h3 className="text-xl font-bold text-[var(--text-primary)]">Site Status</h3>
                    <p className="text-sm text-[var(--text-secondary)]">Analytics coming soon.</p>
                </div>

            </div>
        </div>
    );
}
